import React, { Component } from 'react'
import './TopRated.css'
import Content from '..//Content/Content'
import {TOP_URL, TOP} from '../config'
import {handlerLoading} from '../handlerLoading/handlerLoading'
import {getLocalStorage} from '../localStorage/localStorage'

class TopRated extends Component {
    state = {
        results: [],
        page: 1
    }

    async componentDidMount() {
        if (getLocalStorage(TOP)){
            const localState = getLocalStorage(TOP)
            this.setState({
                results: localState.results,
                page: localState.page
            })
        } else {
            handlerLoading.call(null, this, TOP_URL, TOP)
        } 
    }

    render() {
        return (
            <>
                <h1 className="display-1 mx-auto">Top Rated</h1>
                <div className="container-fluid">
                    <div className="card-deck row justify-content-center my-5">
                        <Content 
                            results={this.state.results}
                            path="/"
                        />
                    </div>
                </div>
                <button 
                className="btn btn-info mx-auto center"
                onClick={handlerLoading.bind(null, this, TOP_URL, TOP)}>Load more..</button>  
            </>
        )
    }
}


export default TopRated;